.website-stores {
  .page-header-intro {
    background-color: var(--primary);

    .wrapper {
      .page-header-content {

        .header-intro-title,
        .header-intro-description {
          color: var(--brand-text-contrast);
        }
      }
    }
  }

  section.stores-list {
    padding: 15px 0;

    .alert {
      &.alert-stores-info {
        margin-bottom: 20px;
        box-shadow: $card-box-shadow;

        ul {
          list-style: none;
          padding: 0;
          margin-bottom: 0;

          li {
            position: relative;
            padding-left: 15px;
            margin-bottom: 5px;
            font-size: 1.05rem;
            line-height: 1.5;

            &:before {
              content: '\f111';
              position: absolute;
              top: 5px;
              left: 0;
              font-family: 'Font Awesome 5 Pro';
              font-size: 8px;
              font-weight: 500;
            }
          }
        }
      }
    }

    .card {
      &.card-stores-list {
        .list-group {
          border-radius: 0.625rem;

          .list-group-item {
            border-right: 0;
            border-left: 0;
						padding: 10px 20px;
						flex-direction: column;
						align-items: flex-start;

            .item-container {
							width: 100%;
							display: flex;
              justify-content: space-between;

              h5 {
								color: var(--primary);
								font-size: 1rem;
                font-weight: 500;
								margin-bottom: 0;

								small{
									font-size: 90%;
									color: #444;
								}
              }

              .badge {
                display: flex;
                height: 100%;
                align-items: center;
                margin-left: 15px;
              }
            }

            &:first-child {
              border-top: 0;
            }

            &:last-child {
              border-bottom: 0;
            }
          }
        }
      }
    }

    .stores-list-map {
      width: 100%;
      height: 80vh;
      border-radius: 0.625rem;
      overflow: hidden;
      box-shadow: $card-box-shadow;
    }
  }
}
